.btn {
      cursor: pointer;
}

.reload-btn {
      width: 120px;
      font-size: 24px;
      font-weight: 700;
      font-weight: 200;
      text-align: center;
      display: flex;
      justify-content: center;
      padding: 8px;
      border: solid 2px #ff6700;
      background: transparent;
      border-radius: 10px;
      letter-spacing: 1px;

      span {
            margin-right: 5px;
            font-size: 24px;
            width: min-content;
            height: max-content;
      }
      &:hover {
            letter-spacing: 2px;
            border: solid 2px #ff8636;
            background: lighten(transparent, 40%);
            span {
                  transform: rotate(-250deg);
            }
      }

      &.reload-comments {
            width: 200px;
            font-size: 16px;
            span {
                  font-size: 16px;
            }
      }
}
